
import { guideList,shareNums} from 'api'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'guideList',
data () {
return {
list: []
}
},
components: {
XButton
},
methods: {
getList(){
guideList().then(res =>{
if(res.code === 0) {
this.list = res.result
}
})
},
changeShareNums(data){
shareNums(data).then(res =>{
console.log(res)
})
},
handleChage(item){
let data = {
id: item.id
}
this.changeShareNums(data)
this.getList()
}
},
mounted () {
this.$nextTick(() =>{
this.getList()
})
},
}



